import React from "react";
import ReactDOM from "react-dom";

const nav = (props) => (
    <ul>
      <li>{props.element1}$</li>
      <li>{props.element2}$</li>
      <li>{props.element3}$</li>
    </ul>
);

ReactDOM.render(
 <nav 
    element1 = "elementt1"
    element2 = "elementt2" 
    element3 = "elementt3"
 />,
    document.getElementById("root")
);